<template>
  <div id="aliapp">
    <router-view/>
    <div class="contactDiv" @mouseenter="showorNot()" @mouseleave="showorNotD()">
      <div class="contact"></div>
      <div class="bridge"></div>
      <transition name="fade">
        <div class="detail" v-if="showDetail">
          <img class="close" src="./indexImages/close.png" @click="showorNot()">
        </div>
      </transition>
    </div>
    <div class="back" v-show="showBack" @click="scrollBack"></div>
  </div>
</template>

<script>
export default {
  name: "aliapp",
  data() {
    return {
      showBack: false,
      showDetail: false
    };
  },
  methods: {
    onScroll: function() {
      var scrollTop = 0;
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop;
      } else if (document.body) {
        scrollTop = document.body.scrollTop;
      }
      if (scrollTop < 500) {
        this.showBack = false;
      } else {
        this.showBack = true;
      }
    },
    scrollBack: function() {
      window.scrollTo(0, 0);
    },
    showorNot: function() {
      this.showDetail = !this.showDetail;
    },
    showorNotD: function() {
      setTimeout(() => {
        this.showDetail = !this.showDetail;
      }, 300);
    }
  },
  mounted() {
    this.$nextTick(function() {
      window.addEventListener("scroll", this.onScroll);
    });
  }
};
</script>

<style scoped>
#aliapp {
  font: Tahoma, Helvetica, Arial, "宋体", sans-serif;
  height: 100%;
  width: 100%;
  min-width: 1200px;
}
.contact {
  background-image: url("./indexImages/zixun.png");
  background-size: 106%;
  background-position: -1px -1px;
  position: fixed;
  bottom: 260px;
  right: 15px;
  width: 40px;
  height: 120px;
  box-shadow: 3px 3px 10px rgba(60, 60, 60, 0.2);
}
.bridge {
  width: 40px;
  height: 120px;
  position: fixed;
  right: 50px;
  bottom: 260px;
}
.detail {
  width: 300px;
  height: 200px;
  position: fixed;
  bottom: 260px;
  right: 70px;
  background-color: white;
  box-shadow: 3px 3px 10px rgba(60, 60, 60, 0.2);
}
.close {
  width: 15px;
  height: 15px;
  float: right;
  margin: 20px;
  cursor: pointer;
}
.back {
  cursor: pointer;
  position: fixed;
  bottom: 30px;
  right: 15px;
  height: 40px;
  width: 40px;
  background-image: url("./indexImages/backToTop.png");
  background-size: 100%;
  box-shadow: 3px 3px 10px rgba(60, 60, 60, 0.2);
}
.fade-enter-active {
  transition: all 0.3s ease;
}
.fade-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



