<template>
  <div id="defence">
    <a href="#">阿里云，今天为全国40%的网站防御了{{count}}次攻击</a>
  </div>
</template>

<script>
export default {
  name: "defence",
  data() {
    return {
      count: 0
    };
  },
  components: {},
  methods: {
    counter: function() {
      setInterval(() => {
        this.count++;
      }, 1000);
    }
  },
  mounted() {
      this.counter();
  }
};
</script>

<style scoped>
#defence {
  width: 100%;
  height: 100px;
  background-image: url("./images/kurumi.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  text-align: center;
}
a:link,a:visited{
    color: white;
    font-size: 30px;
    line-height: 100px;
    text-decoration: none;
    font-weight: 300;
}
</style>


