<template>
  <div id="sMenu">
    <div class="line">
      <div class="listDiv">
        <ul class="list">
          <li class="itemline">
            <div class="item" @click="showContent(1)" :class="{lightUp:showL1[1]}">
              <div class="icon" :class="{black:!showL1[1],color:showL1[1]}"></div>弹性计算
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent(2)" :class="{lightUp:showL1[2]}">
              <div class="icon" :class="{black:!showL1[2],color:showL1[2]}"></div>存储和CDN
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent(3)" :class="{lightUp:showL1[3]}">
              <div class="icon" :class="{black:!showL1[3],color:showL1[3]}"></div>数据库
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent(4)" :class="{lightUp:showL1[4]}">
              <div class="icon" :class="{black:!showL1[4],color:showL1[4]}"></div>网络
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent(5)" :class="{lightUp:showL1[5]}">
              <div class="icon" :class="{black:!showL1[5],color:showL1[5]}"></div>域名与网站
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="aline">
      <div class="triBar">
        <div class="tri" v-show="arrow1" :style="{marginLeft:arrowSet[boxIndex]}"></div>
      </div>
    </div>
    <div class="cline" :style="{height:boxHeight1}">
      <content15 :show="show1[5]"></content15>
      <content14 :show="show1[4]"></content14>
      <content13 :show="show1[3]"></content13>
      <content12 :show="show1[2]"></content12>
      <content11 :show="show1[1]"></content11>
    </div>
    <div class="line">
      <div class="listDiv">
        <ul class="list">
          <li class="itemline">
            <div class="item" @click="showContent2(1)" :class="{lightUp:showL2[1]}">
              <div class="icon" :class="{black:!showL2[1],color:showL2[1]}"></div>安全
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent2(2)" :class="{lightUp:showL2[2]}">
              <div class="icon" :class="{black:!showL2[2],color:showL2[2]}"></div>人工智能ET
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent2(3)" :class="{lightUp:showL2[3]}">
              <div class="icon" :class="{black:!showL2[3],color:showL2[3]}"></div>大数据基础服务
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent2(4)" :class="{lightUp:showL2[4]}">
              <div class="icon" :class="{black:!showL2[4],color:showL2[4]}"></div>大数据应用
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent2(5)" :class="{lightUp:showL2[5]}">
              <div class="icon" :class="{black:!showL2[5],color:showL2[5]}"></div>大数据分析及表现
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="aline">
      <div class="triBar">
        <div class="tri" v-show="arrow2" :style="{marginLeft:arrowSet[boxIndex]}"></div>
      </div>
    </div>
    <div class="cline" :style="{height:boxHeight2}">
      <content21 :show="show2[1]"></content21>
    </div>
    <div class="line">
      <div class="listDiv">
        <ul class="list">
          <li class="itemline">
            <div class="item" @click="showContent3(1)" :class="{lightUp:showL3[1]}">
              <div class="icon" :class="{black:!showL3[1],color:showL3[1]}"></div>分析和搜索
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent3(2)" :class="{lightUp:showL3[2]}">
              <div class="icon" :class="{black:!showL3[2],color:showL3[2]}"></div>云通信
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent3(3)" :class="{lightUp:showL3[3]}">
              <div class="icon" :class="{black:!showL3[3],color:showL3[3]}"></div>应用服务
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent3(4)" :class="{lightUp:showL3[4]}">
              <div class="icon" :class="{black:!showL3[4],color:showL3[4]}"></div>移动云
            </div>
          </li>
          <li class="itemline">
            <div class="item" @click="showContent3(5)" :class="{lightUp:showL3[5]}">
              <div class="icon" :class="{black:!showL3[5],color:showL3[5]}"></div>视频服务
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="aline">
      <div class="triBar">
        <div class="tri" v-show="arrow3" :style="{marginLeft:arrowSet[boxIndex]}"></div>
      </div>
    </div>
    <div class="cline" :style="{height:boxHeight3}">
      <content31 :show="show3[1]"></content31>
    </div>
    <div class="lookAll">
      <div class="lookDist">
        <div class="lookButton">查看全部</div>
      </div>
    </div>
  </div>
</template>

<script>
import content11 from "./contents/content11";
import content12 from "./contents/content12";
import content13 from "./contents/content13";
import content14 from "./contents/content14";
import content15 from "./contents/content15";
import content21 from "./contents/content21";
import content31 from "./contents/content31";
export default {
  name: "sMenu",
  data() {
    return {
      boxIndex: 1,
      boxHeight1: "400px",
      boxHeight2: "0px",
      boxHeight3: "0px",
      boxHeightSet1: ["0px", "400px", "400px", "400px", "400px", "270px"],
      boxHeightSet2: ["0px", "400px", "300px", "300px", "270px", "270px"],
      boxHeightSet3: ["0px", "200px", "300px", "700px", "300px", "100px"],
      arrow1: true,
      arrow2: false,
      arrow3: false,
      arrowSet: ["0%", "9%", "29%", "49%", "69%", "89%"],
      show1: [true, true, false, false, false, false],
      show2: [true, false, false, false, false, false],
      show3: [true, false, false, false, false, false],
      showL1: [true, true, false, false, false, false],
      showL2: [true, false, false, false, false, false],
      showL3: [true, false, false, false, false, false]
    };
  },
  components: {
    content11,
    content12,
    content13,
    content14,
    content15,
    content21,
    content31
  },
  methods: {
    showContent: function(index) {
      this.boxHeight3 = this.boxHeight2 = "0px";
      this.showL2 = this.showL3 = this.show3 = this.show2 = [
        true,
        false,
        false,
        false,
        false,
        false
      ];
      this.boxIndex = index;
      this.boxHeight1 = this.boxHeightSet1[this.boxIndex];
      this.arrow2 = this.arrow3 = false;
      if (this.show1[index] != true) {
        this.showL1 = [true, false, false, false, false, false];
        this.showL1[index] = true;
        this.show1 = [true, false, false, false, false, false];
        setTimeout(() => {
          this.arrow1 = true;
          this.show1.splice(index,1,true);
        }, 300);
      }
    },
    showContent2: function(index) {
      this.boxHeight3 = this.boxHeight1 = "0px";
      this.showL1 = this.showL3 = this.show3 = this.show1 = [
        true,
        false,
        false,
        false,
        false,
        false
      ];
      this.boxIndex = index;
      this.boxHeight2 = this.boxHeightSet2[this.boxIndex];
      this.arrow1 = this.arrow3 = false;
      if (this.show2[index] != true) {
        this.showL2 = [true, false, false, false, false, false];
        this.showL2[index] = true;
        this.show2 = [true, false, false, false, false, false];
        setTimeout(() => {
          this.arrow2 = true;
          this.show2.splice(index,1,true);
        }, 300);
      }
    },
    showContent3: function(index) {
      this.boxHeight1 = this.boxHeight2 = "0px";
      this.showL2 = this.showL1 = this.show1 = this.show2 = [
        true,
        false,
        false,
        false,
        false,
        false
      ];
      this.boxIndex = index;
      this.boxHeight3 = this.boxHeightSet3[this.boxIndex];
      this.arrow1 = this.arrow2 = false;
      if (this.show3[index] != true) {
        this.showL3 = [true, false, false, false, false, false];
        this.showL3[index] = true;
        this.show3 = [true, false, false, false, false, false];
        setTimeout(() => {
          this.arrow3 = true;
          this.show3.splice(index,1,true);
        }, 300);
      }
    }
  }
};
</script>

<style scoped>
#sMenu {
  width: 100%;
  min-width: 1020px;
}
.strangeThing {
  display: none;
}
.line {
  height: 120px;
  width: 80%;
  margin: 20px auto 0px auto;
}
.listDiv {
  height: 100%;
  width: 80%;
  margin: 0px auto;
}
.list {
  height: 100%;
  width: 100%;
  margin: 0px auto;
  list-style: none;
}
.itemLine {
  display: inline;
}
.item {
  cursor: pointer;
  height: 120px;
  width: 14%;
  margin: 0px 3%;
  float: left;
  text-align: center;
  line-height: 20px;
  font-weight: 400;
  font-size: 13px;
  text-decoration: none;
  color: rgb(60, 60, 60);
  transition: 0.3s;
}
.lightUp {
  color: #66cae2;
}
.icon {
  width: 80px;
  height: 80px;
  margin: 5px auto;
  background-size: 100%;
  transition: 0.3s;
}
.black {
  background-image: url("./images/yzl.png");
}
.color {
  background-image: url("./images/yzlc.png");
}
.item:hover {
  transition: 0.3s;
  color: #66cae2;
}
.aline {
  width: 100%;
  height: 10px;
}
.triBar {
  height: 100%;
  width: 64%;
  margin: 0px auto;
}
.tri {
  transition: 0.3s;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid rgb(245, 245, 245);
}
.cline {
  width: 100%;
  background-color: rgb(245, 245, 245);
  transition: 0.3s;
  transition-delay: 0.1s;
}
.lookAll {
  width: 100%;
  min-width: 1020px;
  height: 100px;
}
.lookDist {
  width: 120px;
  height: 100%;
  margin: 0px auto;
  padding-top: 35px;
}
.lookButton {
  width: 100%;
  height: 35px;
  outline: 1px solid black;
  text-align: center;
  line-height: 35px;
  font-weight: 300;
  font-size: 15px;
}
.lookButton:hover {
  outline: 1px solid #66cae2;
  color: #66cae2;
}
</style>


