<template>
  <div id="worldMap">
    <div class="map">
      <div class="oneDot positionSize1">
        <div class="dot"></div>
        <p>香港</p>
        <div class="circle"></div>
        <div class="circle" v-if="show"></div>
        <div class="circle circle3" v-if="show2"></div>
      </div>
      <div class="oneDot positionSize2">
        <div class="dot"></div>
        <p>华南</p>
        <div class="circle"></div>
        <div class="circle" v-if="show"></div>
        <div class="circle circle3" v-if="show2"></div>
      </div>
      <div class="oneDot positionSize3">
        <div class="dot"></div>
        <p>日本</p>
        <div class="circle"></div>
        <div class="circle" v-if="show2"></div>
      </div>
      <div class="oneDot positionSize4">
        <div class="dot"></div>
        <p>美西</p>
        <div class="circle"></div>
        <div class="circle" v-if="show2"></div>
      </div>
      <div class="oneDot positionSize5">
        <div class="dot"></div>
        <p>美东</p>
        <div class="circle"></div>
        <div class="circle" v-if="show"></div>
        <div class="circle circle3" v-if="show2"></div>
      </div>
    </div>
    <div class="icons">
      <div class="oneicon">
        <img src="./images/safty1.png">
        <p>全球首张云安全国际认证</p>
      </div>
      <div class="oneicon">
        <img src="./images/safty2.png">
        <p>信息安全管理体系国际认证</p>
      </div>
      <div class="oneicon">
        <img src="./images/safty3.png">
        <p>通过公安部等级保护评测</p>
      </div>
      <div class="oneicon">
        <img src="./images/safty4.png">
        <p>首批可信云服务认证</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "worldMap",
  data() {
    return {
      show: false,
      show2: false
    };
  },
  components: {},
  methods: {
    timer() {
      setTimeout(() => {
        this.show = true;
        setTimeout(() => {
          this.show2 = true;
        }, 600);
      }, 600);
    }
  },
  mounted: function() {
    this.timer();
  }
};
</script>

<style scoped>
#worldMap {
  background-color: rgb(245, 245, 245);
  width: 100%;
  height: 700px;
}
.map {
  height: 550px;
  width: 1200px;
  margin: 0px auto;
  background-image: url("./images/worldmap.png");
  background-size: 100%;
  background-repeat: no-repeat;
  margin-bottom: 20px;
  position: relative;
}
.oneDot {
  position: absolute;
}
.oneDot p {
  position: absolute;
  left: 57%;
  top: 44%;
  font-size: 12px;
  font-weight: 300;
}
.positionSize1 {
  height: 140px;
  width: 140px;
  left: 72%;
  top: 160px;
}
.positionSize2 {
  height: 200px;
  width: 200px;
  left: 68.5%;
  top: 120px;
}
.positionSize3 {
  height: 100px;
  width: 100px;
  left: 80%;
  top: 130px;
}
.positionSize4 {
  height: 100px;
  width: 100px;
  left: 11%;
  top: 110px;
}
.positionSize5 {
  height: 120px;
  width: 120px;
  left: 22%;
  top: 115px;
}
.dot {
  width: 10px;
  height: 10px;
  background-color: #66cae2;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -4px;
  margin-left: -4px;
}
.circle {
  border: 1px solid #66cae2;
  border-radius: 50%;
  position: absolute;
  animation: explode 6s infinite;
}
.circle3 {
  background-color: rgba(134, 213, 230, 0.2);
}
.icons {
  cursor: pointer;
  width: 70%;
  height: 50px;
  margin: 0px auto;
}
.oneicon {
  width: 25%;
  height: 100%;
  float: left;
}
.oneicon img {
  margin-left: 30px;
  height: 50px;
  width: auto;
  float: left;
}
.oneicon p {
  float: left;
  line-height: 50px;
  font-size: 12px;
  font-weight: 300;
  margin-left: 10px;
}
@keyframes explode {
  0% {
    width: 0px;
    height: 0px;
    left: 50%;
    top: 50%;
    opacity: 100%;
  }
  100% {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
  }
}
</style>


