<template>
  <div id="slide">
    <div class="window">
      <div class="line" @mouseenter="showButton=true" @mouseleave="showButton=false">
        <transition name="fade">
          <div class="button button1" v-if="showButton" @click="left()">
            <div class="arrow"></div>
          </div>
        </transition>
        <transition name="fade">
          <div class="button button2" v-if="showButton" @click="right()">
            <div class="arrow rotate"></div>
          </div>
        </transition>
        <div class="item" :style="{left:position[0]+'%'}" :class="{anim:animation}"><img src="./images/bg1.jpg"></div>
        <div class="item" :style="{left:position[1]+'%'}" :class="{anim:animation}"><img src="./images/bg2.jpg"></div>
        <div class="item" :style="{left:position[2]+'%'}" :class="{anim:animation}"><img src="./images/bg3.jpg"></div>
        <div class="item" :style="{left:position[3]+'%'}" :class="{anim:animation}"><img src="./images/bg4.jpg"></div>
        <div class="item" :style="{left:position[4]+'%'}" :class="{anim:animation}"><img src="./images/bg5.jpg"></div>
        <div class="item" :style="{left:position[5]+'%'}" :class="{anim:animation}"><img src="./images/bg6.jpg"></div>
        <div class="item" :style="{left:position[6]+'%'}" :class="{anim:animation}"><img src="./images/bg7.jpg"></div>
        <div class="item" :style="{left:position[7]+'%'}" :class="{anim:animation}"><img src="./images/bg8.jpg"></div>
        <div class="item" :style="{left:position[8]+'%'}" :class="{anim:animation}"><img src="./images/bg9.jpg"></div>
        <div class="item" :style="{left:position[9]+'%'}" :class="{anim:animation}"><img src="./images/bg10.jpg"></div>
        <div class="item" :style="{left:position[10]+'%'}" :class="{anim:animation}"><img src="./images/bg11.jpg"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "slide",
  data() {
    return {
      showButton: false,
      numberOfCards: 11,
      position: [0, 20, 40, 60, 80, 100, 120, 140, 160, 180, 200],
      first: 0,
      animation: false
    };
  },
  components: {},
  methods: {
    left: function() {
      this.animation = false;
      let move = 100;
      if (this.first == 1) {
        this.position.splice(0, 1, -20);
        move = 20;
        this.first = 0;
      } else {
        for (var i = 0; i < 11; i++) {
          if (this.position[i] >= 120) {
            this.position.splice(i, 1, this.position[i] - 220);
          }
        }
        this.first = (this.first + 6) % 11;
      }
      setTimeout(() => {
        this.animation = true;
        for (var i = 0; i < 11; i++) {
          this.position.splice(i, 1, this.position[i] + move);
        }
      }, 1);
    },
    right: function() {
      this.animation = false;
      let move = 100;
      if (this.first == 5) {
        this.position.splice(10, 1, 100);
        move = 20;
        this.first = 10;
      } else {
        for (var i = 0; i < 11; i++) {
          if (this.position[i] <= -40) {
            this.position.splice(i, 1, (this.position[i] += 220));
          }
        }
        this.first = (this.first + 5)%15;
      }
      setTimeout(() => {
        this.animation = true;
        for (var i = 0; i < 11; i++) {
          this.position.splice(i, 1, this.position[i] - move);
        }
      }, 1);
    }
  }
};
</script>

<style scoped>
#slide {
  height: 420px;
  width: 100%;
  min-width: 1050px;
  background-color: rgb(51, 61, 64);
  position: relative;
  left: 0;
}
.window {
  position: relative;
  left: 0;
  height: 100%;
  width: 80%;
  margin: 0px auto;
  z-index: 0;
  overflow: hidden;
}
.button {
  cursor: pointer;
  height: 420px;
  width: 50px;
  background-color: rgba(120, 120, 120, 0.3);
  position: absolute;
}
.button1 {
  left: 0%;
}
.button2 {
  right: 0%;
}
.line {
  width: 100%;
  height: 450px;
  margin: 0px;
}
.arrow {
  width: 50px;
  height: 50px;
  margin: 185px 0px;
  background-image: url("./images/arrow.png");
  background-size: 100%;
  position: absolute;
}
.rotate {
  transform: rotate(180deg);
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: 0.3s;
}
.item {
  cursor: pointer;
  height: 420px;
  width: 20%;
  position: absolute;
  background-color: antiquewhite;
  float: left;
  outline: 1px solid black;
  z-index: -9999;
  top: 0px;
  overflow: hidden;
}
.anim {
  transition: 0.6s;
}
.item img{
    height: 130%;
    width: auto;
}
</style>


